import Score from '../../components/Score.vue';
export default {
	components: {
		Score
	},
	data() {
		return {
			swipeImgUrl: [],
			hotImg: '../../static/icos/hot.png',
			hotImgUrl: [],
		}
	},
	onLoad() {
		this.getSwiper();
		this.getHotData();
	},
	methods: {
		// TODO 获取轮播图数据
		getSwiper () {
			uni.request({
				url: `${this.serverURL}/index/carousel/list`,
				method: "POST",
				header:this.header,
				data: {
					qq: this.qq
				},
				success: (res) => {
					if (res.data.status === 200) {
						this.swipeImgUrl = res.data.data;
					}
				}
			});
		},
		// TODO 获取热门推荐
		getHotData () {
			uni.request({
				url: `${this.serverURL}/index/movie/hot`,
				method: "POST",
				header: this.header,
				data: {
					type: 'superhero',
					qq: this.qq
				},
				success: (res) => {
					if (res.data.status === 200) {
						let data = res.data.data;
						this.hotImgUrl = data;
					}
				}
			});
		}
	}
}
